import React, { Suspense, useState } from "react";
import { Card, CardBody, Table } from "react-bootstrap"
import ScreenoverviewSkeleton from "../../../layout/skeleton/screenoverviewSkeleton";
import NoData from "../../../layout/common/noData";
import SectionHeading from "../../../layout/common/sectionHeading";

const PopupModal = React.lazy(() =>
  import("../../../layout/common/popup/popupModal")
);

const ScreenOverview = React.lazy(() =>
  import("../../../layout/popup/screenOverview")
);


const ScreensOverview = (props) => {

  const { data, total, loader } = props

  const [show, setShow] = useState(false)

  const handlePopup = event => {
    setShow(event)
  }

  return (
    <>
      <Card className="sw-card-custom gutter-b card-shadow">
        <SectionHeading section={2} moreData={total > 15 ? 1 : null} handlePopup={handlePopup} title={'Screens Overview'} />
        <CardBody className="py-5 px-0">
          <Table responsive>
            <thead>
              <tr>
                <th className="text-muted" style={{ width: "16%" }}>
                  Screen Name
                </th>
                <th className="text-muted" style={{ width: "16%" }}>
                  Screen IP
                </th>
                <th className="text-muted" style={{ width: "16%" }}>
                  Device Location
                </th>
                <th className="text-muted" style={{ width: "16%" }}>
                  Status
                </th>
                <th className="text-muted" style={{ width: "16%" }}>
                  Last Refresh Time
                </th>
                <th className="text-muted" style={{ width: "16%" }}>
                  Daily Active Hours
                </th>
              </tr>
            </thead>
            <tbody>


              {loader ? <ScreenoverviewSkeleton /> : data && Object.keys(data).length ? data.map((e) => {

                return (<tr className="border-top">
                  <td>
                    <strong>{e.screenName}</strong>
                  </td>
                  <td><div className="">{e.screenIp}</div></td>
                  <td>
                    {e.deviceLocation}
                  </td>
                  <td>
                    <label className={`mx-width-80 alert ${e.status == "Inactive" ? 'alert-danger' : 'alert-success'} m-0 rounded-0 px-3 py-1`}>
                      {e.status}
                    </label>
                  </td>
                  <td>
                    {e.lastRefreshTime}
                  </td>
                  <td>
                    {e.activeHours} Hours
                  </td>
                </tr>
                )
              })
                : <NoData status={1} col={6} />
              }
            </tbody>
          </Table>
        </CardBody>
      </Card>


      {total > 15 ?
        <PopupModal size={'xl'} showHide={show} handlePopup={handlePopup} title={'Screens Overview'}>
          <Suspense>
            <ScreenOverview total={total} {...props} />
          </Suspense>
        </PopupModal> : null}
    </>
  )
}

export default React.memo(ScreensOverview);