import React from "react";

const List = () => {
  return (
    <td>
      <div>
        <div className="sk_line sk_line_30 sk_line_height_16"></div>
      </div>
    </td>
  );
};

const ScreenoverviewSkeleton = () => {
  return [1, 2, 3, 4].map(() => {
    return (
      <tr>
        <List />
        <List />
        <List />
        <List />
        <List />
        <List />
      </tr>
    );
  });
};

export default React.memo(ScreenoverviewSkeleton);
